:root {
  --sans: "DM Sans", sans-serif;
  --display: "Playfair Display", serif;
  --lato: "Lato", sans-serif;
  --poppins: "Poppins", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --pastel-pink: hsl(27, 100%, 82%);
  --pastel-green: hsl(134, 13%, 33%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  margin: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  object-fit: contain;
}

.large {
  display: none !important;
}

.med-desk {
  display: none !important;
}

@media (min-width: 40em) {
  .med-desk {
    display: initial !important;
  }

  .mobile {
    display: none !important;
  }
}
@media (min-width: 67.5em) {
  .large {
    display: initial !important;
  }

  .medium {
    display: none !important;
  }
}
h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

p {
  font-family: var(--sans);
  font-style: normal;
  font-weight: normal;
}

h2 {
  font-family: var(--display);
  font-style: normal;
  font-weight: 600;
  font-size: 5rem;
  line-height: 5.625rem;
}

body {
  background-color: var(--light-gray);
  position: relative;
}

.container {
  padding: 0 1.5rem;
  margin: 0 auto;
  width: 100%;
  position: relative;
}
@media (min-width: 40em) {
  .container {
    padding: 0 4rem;
  }
}
@media (min-width: 80.25em) {
  .container {
    padding: 0;
    max-width: 76.25rem;
  }
}

.content {
  position: relative;
  width: 100%;
}

:root {
  --slide-fade-time: 1.5s;
  --no-of-carousel-image: 3;
  --current-carousel-image: 2;
}

.header {
  background-color: var(--pastel-green);
  font-weight: 500;
  font-size: 1rem;
  position: relative;
}
.header__logo {
  z-index: 3;
  grid-area: lo;
  margin: 0 1rem;
  width: fit-content;
}
.header__logo img {
  z-index: 3;
  width: 6.5625rem;
}
.header .navbar {
  z-index: 2;
  padding-top: 1.875em;
  padding-bottom: 1.25em;
  display: grid;
  grid-template-columns: auto 1fr auto;
  text-transform: uppercase;
  grid-template-areas: "mn lo op";
  align-items: center;
}
.header .nav {
  grid-area: nv;
  position: absolute;
  right: 0;
  top: -100vh;
  width: 100%;
  height: 100vh;
  z-index: 2;
  padding: 0 1.5em;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.0001) 100%);
  mix-blend-mode: normal;
  transition: all ease-in-out 0.3s;
}
.header .nav.open {
  top: 0;
}
.header .nav__list {
  list-style: none;
  width: 100%;
  margin-top: 5.5rem;
  background: white;
  padding: 0;
  border-radius: 0.5rem;
}
.header .nav__list .navlink:not(:last-child) {
  border-bottom: 1px solid rgba(47, 47, 47, 0.1);
}
.header .nav__list .navlink a {
  position: relative;
  text-decoration: none;
  color: #000;
  display: inline-block;
  width: 100%;
  padding: 1.5em;
  font-family: var(--sans);
  font-style: normal;
  font-weight: 500;
  font-size: 0.9375em;
  line-height: 1.2;
  overflow: hidden;
  transition: all ease-in-out 0.3s;
}
.header .nav__list .navlink a::before {
  content: "";
  position: absolute;
  bottom: 46%;
  left: 0px;
  width: 100%;
  height: 1px;
  background: var(--pastel-pink);
  display: block;
  -webkit-transform-origin: left top;
  -ms-transform-origin: left top;
  transform-origin: left top;
  -webkit-transform: scale(0, 1);
  -ms-transform: scale(0, 1);
  transform: scale(0, 1);
  -webkit-transition: transform 0.3s cubic-bezier(1, 0, 0, 1);
  transition: transform 0.3s cubic-bezier(1, 0, 0, 1);
}
.header .nav__list .navlink a:hover::before {
  -webkit-transform-origin: right top;
  -ms-transform-origin: right top;
  transform-origin: right top;
  -webkit-transform: scale(1, 1);
  -ms-transform: scale(1, 1);
  transform: scale(1, 1);
}
.header .nav__list .navlink a:hover, .header .nav__list .navlink a:focus {
  color: var(--pastel-pink);
}
.header .nav__options {
  display: flex;
  align-items: center;
  grid-area: op;
}
.header .nav__options .localization {
  z-index: 3;
}
.header .nav__options .localization select {
  background: transparent;
  outline: none;
  color: var(--white);
  border: none;
  font-size: 0.9375em;
}
.header .nav__options .localization select option {
  color: var(--black);
}
.header .nav__options .search {
  z-index: 5;
  margin-left: 1.1875rem;
}
.header .nav__options .search svg {
  cursor: pointer;
  stroke: var(--white);
}
.header .nav__options .search svg:hover {
  stroke: var(--pastel-pink);
}
.header__menuControls {
  grid-area: mn;
  z-index: 3;
  cursor: pointer;
}
.header__menuControls svg {
  width: 1.25rem;
}
.header__menuControls .closeMenu {
  display: none;
}
.header__menuControls .burger {
  display: inline-block;
}
.header__menuControls.open .closeMenu {
  display: inline-block;
}
.header__menuControls.open .burger {
  display: none;
}
@media (min-width: 40em) {
  .header {
    font-size: 0.8rem;
  }
  .header__logo img {
    width: 9.4375em;
  }
  .header__menuControls {
    display: none;
  }
  .header .navbar {
    padding-top: 3rem;
    padding-bottom: 3rem;
    display: grid;
    grid-template-columns: auto 1fr auto;
    grid-template-areas: "lo nv op";
    align-items: center;
    justify-content: center;
  }
  .header .nav {
    position: static;
    padding: 0;
    background: none;
    width: initial;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: auto;
    margin: 0 auto;
  }
  .header .nav__list {
    width: initial;
    margin: 0;
    background: none;
    border-radius: 0;
    display: flex;
    align-items: center;
    gap: 2.8125em;
  }
  .header .nav__list .navlink {
    border: none;
  }
  .header .nav__list .navlink a {
    color: white;
    font-size: 0.9375em;
    line-height: 1.25em;
    padding: 0.1875em 0.5em;
  }
  .header .nav__options {
    display: flex;
    align-items: center;
  }
}
@media (min-width: 67.5em) {
  .header {
    font-size: 1rem;
  }
  .header .nav-list {
    gap: 3.5em;
  }
}

