.carousel {
  font-size: 0.72rem;
  margin-top: -3rem;
  /* Hide the images by default */
  /* Fading animation */
}
.carousel .slideshow-container {
  width: 100%;
  position: relative;
  margin: auto;
  background-color: var(--white);
}
.carousel .slideshow-controls {
  padding: 3.875em 0;
  margin-top: 0.875em;
  position: relative;
}
.carousel .slideshow-controls .controls {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.carousel .slideshow-controls .controls .prev,
.carousel .slideshow-controls .controls .next {
  cursor: pointer;
}
.carousel .slideshow-controls .controls .prev svg,
.carousel .slideshow-controls .controls .next svg {
  width: 1.8125em;
}
.carousel .slideshow-controls .controls .prev:hover svg,
.carousel .slideshow-controls .controls .next:hover svg {
  stroke: var(--pastel-pink);
}
.carousel .slideshow-controls .progress {
  width: 70%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  gap: 0.625rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: var(--lato);
  font-style: normal;
  font-weight: normal;
  font-size: 0.9375em;
  line-height: 2.3;
}
.carousel .slideshow-controls .progress span {
  display: none;
}
.carousel .slideshow-controls .progress-bar {
  width: 100%;
  height: 0.0625rem;
  background-color: rgba(0, 0, 0, 0.4);
  position: relative;
}
.carousel .slideshow-controls .progress-bar::before {
  content: "";
  width: calc((1 / var(--no-of-carousel-image)) * 100%);
  height: 0.1875rem;
  background: var(--black);
  opacity: 1;
  position: absolute;
  left: calc( ( (var(--current-carousel-image) - 1) / var(--no-of-carousel-image) ) * 100% );
  top: 50%;
  transform: translateY(-50%);
  transition: left ease-in-out 0.3s;
}
.carousel .slideshow-text {
  font-family: var(--sans);
  font-style: normal;
  font-weight: 500;
  font-size: 1.5625em;
  line-height: 1.6;
}
.carousel .mySlides {
  display: none;
}
.carousel .fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: var(--slide-fade-time);
  animation-name: fade;
  animation-duration: var(--slide-fade-time);
}
@-webkit-keyframes fade {
  from {
    opacity: 0.7;
  }
  to {
    opacity: 1;
  }
}
@keyframes fade {
  from {
    opacity: 0.7;
  }
  to {
    opacity: 1;
  }
}
@media (min-width: 40em) {
  .carousel {
    font-size: 0.8rem;
    margin-top: -13rem;
  }
  .carousel .slideshow-controls {
    display: flex;
