  line-height: 1.8;
  background-color: var(--pastel-green);
  color: white;
  padding: 3.75rem 0;
}
.footer .content {
  text-align: center;
}
.footer .content > * {
  margin-bottom: 2rem;
}
.footer .content .footer-socials {
  display: flex;
  width: fit-content;
  margin: 0 auto 3rem;
  gap: 2rem;
}
.footer .content .footer-socials li a svg {
  fill: white;
  transition: all ease-in-out 0.2s;
}
.footer .content .footer-socials li a svg:hover {
  fill: var(--pastel-pink);
}
.footer .content p {
  font-weight: bold;
  font-size: 1.0625em;
  line-height: 1.6;
  margin-bottom: 0.5em;
}
.footer ul {
  list-style: none;
}
.footer ul li a:hover {
  color: var(--pastel-pink);
}
.footer .attribution {
  font-size: 0.8em;
  text-align: center;
}
.footer .attribution a {
  cursor: pointer;
}
.footer .attribution a svg {
  display: inline-block;
  margin-bottom: -0.5em;
}
.footer .attribution a svg:hover {
  fill: var(--pastel-pink);
}
@media (min-width: 40em) {
  .footer {
    font-size: 0.9rem;
    padding: 6.25rem 0;
  }
  .footer .content {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr 1fr;
    grid-template-rows: 3.75em auto auto auto;
    grid-template-areas: "lg cp le rs" "ad cp le rs" "sc . . .";
    column-gap: 4rem;
    align-items: start;
  }
  .footer .content .footer-logo {
    grid-area: lg;
  }
  .footer .content .footer-address {
    grid-area: ad;
  }
  .footer .content .footer-company {
    grid-area: cp;
  }
  .footer .content .footer-resources {
    grid-area: rs;
  }
  .footer .content .footer-legal {
    grid-area: le;
  }
  .footer .content .footer-socials {
    grid-area: sc;
  }
}
@media (min-width: 67.5em) {
  .footer {
    font-size: 1rem;
  }
  .footer .content {
    grid-template-columns: 40% 1fr 1fr 1fr;
  }
}
@media (min-width: 80.25em) {
  .footer {
    font-size: 1.125rem;
  }
}